<style>
#design-illustration-m .kube {
  opacity: 0;
}

@keyframes fade-in {
  to {
    opacity: 1
  }
}

@keyframes animate-kube-top-left {
  to {
    opacity: 1;
    transform: translate(246.494px, 486.457px) rotate(-90deg);
  }
}

@keyframes animate-kube-bottom-left {
  to {
    opacity: 1;
    transform: translate(564.93px, 845.978px) rotate(-180deg);
  }
}

@keyframes animate-kube-top-right {
  to {
    opacity: 1;
    transform: translate(606px, 166px) rotate(0deg);
  }
}

@keyframes animate-kube-bottom-right {
  to {
    opacity: 1;
    transform: translate(927px, 528px) rotate(90deg);
  }
}

@keyframes dash-right {
  from {
    stroke-dashoffset: -500;
  }
  to {
    stroke-dashoffset: 0;
  }
}

@keyframes dash-left {
  from {
    stroke-dashoffset: 500;
  }
  to {
    stroke-dashoffset: 0;
  }
}

@keyframes dash-reverse-right {
  from {
    stroke-dashoffset: 0;
  }
  to {
    stroke-dashoffset: -500;
  }
}

@keyframes dash-reverse-left {
  from {
    stroke-dashoffset: 0;
  }
  to {
    stroke-dashoffset: 500;
  }
}

#design-illustration-m > #kube-top-left {
  transform: translate(100px, 100px) rotate(-90deg);
  animation: animate-kube-top-left 1.5s ease forwards;
}

#design-illustration-m > #kube-bottom-left {
  transform: translate(500px, 500px) rotate(-180deg);
  animation: animate-kube-bottom-left 1.5s ease forwards;
}

#design-illustration-m > #kube-top-right {
  transform: translate(500px, 500px) rotate(0deg);
  animation: animate-kube-top-right 1.5s ease forwards;
}

#design-illustration-m > #kube-bottom-right {
  transform: translate(500px, 500px) rotate(90deg);
  animation: animate-kube-bottom-right 1.5s ease forwards;
}

#design-illustration-m .line {
  stroke-miterlimit: 10;
  stroke-dasharray: 500;
}

#design-illustration-m > #line-top-right, #design-illustration-m > #line-bottom-right {
  animation: dash-right 1s linear forwards;
}

#design-illustration-m > #line-top-left, #design-illustration-m > #line-bottom-left  {
  animation: dash-left 1s linear forwards;
}

</style>
