<template>
  <div class="services-mobile">
    <div class="design service">
      <h1 id="design-title">Design</h1>
      <design-illustration-mobile/>
      <p class="description-m">
        kube10 designs anything and everything for your web-related projects, including logo's, UI, illustrations, animations and icons.
      </p>
    </div>
    <div class="coding service">
      <h1 id="coding-title">Coding</h1>
      <coding-illustration-mobile/>
      <p class="description-m">
         We write custom code that will bring your vision to life. Based on your needs, we use a CMS like WordPress, or build your website completely from scratch.
      </p>
    </div>
    <div class="content service">
      <h1 id="content-title">Content</h1>
      <content-illustration-mobile/>
      <p class="description-m">
        Using our network of creative minds, we can help you come up with great content for your customers, that will keep them engaged.
      </p>
    </div>
    <div class="support service">
      <h1 id="support-title">Support</h1>
      <support-illustration-mobile/>
      <p class="description-m">
        kube10 offers continuous support after publishing your website. This way we guarantee your project remains future proof.
      </p>
    </div>
  </div>
</template>

<script>
import $ from 'jquery'
import DesignIllustrationMobile from '@/components/mobile-service-illustrations/design-illustration-mobile.vue'
import CodingIllustrationMobile from '@/components/mobile-service-illustrations/coding-illustration-mobile.vue'
import ContentIllustrationMobile from '@/components/mobile-service-illustrations/content-illustration-mobile.vue'
import SupportIllustrationMobile from '@/components/mobile-service-illustrations/support-illustration-mobile.vue'

  export default {
    name: 'ServicesMobile',
    components: {
      DesignIllustrationMobile,
      CodingIllustrationMobile,
      ContentIllustrationMobile,
      SupportIllustrationMobile
    },
    methods:  {
      animateIllustrations(st) {
        if (st > $('#support-title').offset().top - 250) {
          $('#support-illustration-m').addClass('active');
        } else if (st > $('#content-title').offset().top - 250) {
          $('#content-illustration-m').addClass('active');
        } else if (st > $('#coding-title').offset().top - 250) {
          $('#coding-illustration-m').addClass('active');
        }
      }
    },
    mounted() {
      var vm = this;
      $('.logo .small').css('background-color', 'white');
      $(window).scroll(function(){
        var st = $(window).scrollTop();
        vm.animateIllustrations(st);
      });
    }
  }
</script>

<style>
.service h1 {
  font-weight: 900;
  text-align: center;
  font-size: 3.5em;
  text-transform: uppercase;
}

.description-m {
  padding-left: 10px;
  padding-right: 20px;
}

.support > .description-m {
  margin-bottom: 75px;
}

#design-title {
  color: #FF6464;
}

#coding-title {
  color: #88D483;
}

#content-title {
  color: #FEBC1E;
}

#support-title {
  color: #527BCE;
}

.service-illustration-mobile {
  width: 100vw;
  height: 30vh;
}
</style>
