<template>
  <div class="services">
    <div class="titles">
      <div id="3" class="title"> {{services[3]["title"]}}</div>
      <div id="2" class="title">{{services[2]["title"]}}</div>
      <div id="1" class="title">{{services[1]["title"]}}</div>
      <div id="0" class="title">{{services[0]["title"]}}</div>
    </div>
    <div class="description"></div>
    <services-illustration/>
    <svg id="click-us" xmlns="http://www.w3.org/2000/svg" width="140.31" height="192.482" viewBox="0 0 140.31 192.482">
      <g id="click-us-icon" transform="translate(-1329.949 -387.949)">
        <g id="Ellipse_12" data-name="Ellipse 12" transform="translate(1423.096 387.949) rotate(76)" fill="#fff" stroke="#000" stroke-width="3">
          <circle cx="47.999" cy="47.999" r="47.999" stroke="none"/>
          <circle cx="47.999" cy="47.999" r="46.499" fill="none"/>
        </g>
        <text id="click_us" data-name="click
    us" transform="matrix(1, 0.017, -0.017, 1, 1387.682, 443.903)" font-size="23" font-family="Lato-Bold, Lato" font-weight="700"><tspan x="-23.011" y="0">click</tspan><tspan x="-11.54" y="23">us</tspan></text>
        <path id="Path_243" data-name="Path 243" d="M9392.608,2750.965s-13.017,72.182,81.647,88.749" transform="translate(-8004.256 -2260.761)" fill="none" stroke="#000" stroke-width="3"/>
      </g>
    </svg>

  </div>
</template>

<script>
import $ from 'jquery'
import ServicesIllustration from '@/components/ServicesIllustration.vue'
import IsMobileMixin from '@/mixins/IsMobileMixin.js'

  export default {
    components: {
      ServicesIllustration
    },
    mixins: [
      IsMobileMixin
    ],
    data() {
      return {
        activeService: "",
        services: [
          {
            title: "Design",
            color: "#FF6464",
            description: "kube10 designs anything and everything for your web-related projects, including logo's, UI, illustrations, animations and icons."
          },
          {
            title: "Coding",
            color: "#88D483",
            description: "We write custom code that will bring your vision to life. Based on your needs, we use a CMS like WordPress, or build your website completely from scratch."
          },
          {
            title: "Content",
            color: "#FEBC1E",
            description: "Using our network of creative minds, we can help you come up with great content for your customers, that will keep them engaged."
          },
          {
            title: "Support",
            color: "#527BCE",
            description: "kube10 offers continuous support after publishing your website. This way we guarantee your project remains future proof."
          }
        ]
      }
    },
    methods: {
      animateIllustration(id, delay) {
        $('.illustration-group').removeClass('active');
        setTimeout(function () {
          if (id === "0") {
            $('#design-illustration').addClass('active');
          } else if (id === "1") {
            $('#coding-illustration').removeClass('base');
            $('#coding-illustration').addClass('active');
          } else if (id === "2") {
            $("#content-illustration").removeClass('base');
            $('#content-illustration').addClass('active');
          } else if (id === "3") {
            $("#support-illustration").removeClass('base');
            $('#support-illustration').addClass('active');
          }
        }, delay);
      },
      animateTitle(id) {
        $("#" + id + ".title").css('color', this.services[id]['color']);
        $("#" + id + ".title").css('font-size', '8em');
      },
      removeAnimateTitle() {
        $(".title").css('color', "black");
        $(".title").css('font-size', '3.8em');
      },
      animateDescription(id) {
        var vm = this;
        $('.description').animate({
          opacity: 0,
          left: "55%"
        }, 300, function() {
          $('.description').html(vm.services[id]['description']);
          $('.description').animate({
            opacity: 1,
            left: "60%"
          }, 300);
        });
      }
    },
    created() {
      if (this.isMobile()) {
        this.$router.push('/services-m');
      }
    },
    mounted() {
      var vm = this;
      vm.removeAnimateTitle();
      vm.animateTitle(0);
      vm.animateDescription(0);
      vm.animateIllustration("0", 0);
      $('.logo .small').css('background-color', '#F5F5F5');
      $(".title").on('click', function(){
        var i = $(this).attr('id');
        $('#click-us').css('opacity', '0');
        vm.removeAnimateTitle();
        vm.animateTitle(i);
        vm.animateDescription(i);
        vm.animateIllustration(i, 500);
      });
    }
  }
</script>

<style>
  .titles {
    position: absolute;
    bottom: 10px;
    right: 20px;
  }

   .title:nth-child(1):hover {
     color: #527BCE !important;
   }

   .title:nth-child(2):hover {
     color: #FEBC1E !important;
   }

   .title:nth-child(3):hover {
     color: #88D483 !important;
   }

   .title:nth-child(4):hover {
     color: #FF6464 !important;
   }

  .description {
    font-size: 1.2em;
    width: 400px;
    line-height: 1.617;
    position: absolute;
    top: 40%;
    transform: translateY(-50%);
    left: 60%;
    font-family: 'Lato', sans-serif;
  }

  .title {
    text-transform: uppercase;
    font-weight: 900;
    text-align: right;
    font-size: 3.8em;
    transition: font-size .5s ease-in, color .3s ease;
    cursor: pointer;
  }

  #click-us {
    position: absolute;
    left: 65%;
    top: 55%;
    transform: scale(.7);
    transition: opacity .2s ease;
  }

  @media (min-width: 1536px) {
    #click-us {
      left: 67%;
      top: 60%;
    }
  }

  @media (min-width: 1920px) {
    #click-us {
      left: 70%;
    }
  }

</style>
